
	.image-picker.svelte-ikvep {
		width: 100%;
		max-width: 600px;
		border: 1px solid #e5e7eb;
		border-radius: 8px;
		background: white;
		overflow: hidden;
	}

	.tab-navigation.svelte-ikvep {
		display: flex;
		border-bottom: 1px solid #e5e7eb;
		background: #f9fafb;
	}

	.tab-button.svelte-ikvep {
		flex: 1;
		padding: 12px 16px;
		border: none;
		background: transparent;
		color: #6b7280;
		font-weight: 500;
		cursor: pointer;
		transition: all 200ms;
		border-bottom: 2px solid transparent;
	}

	.tab-button.svelte-ikvep:hover {
		background: #f3f4f6;
		color: #374151;
	}

	.tab-button.active.svelte-ikvep {
		color: #2563eb;
		border-bottom-color: #2563eb;
		background: white;
	}

	.tab-content.svelte-ikvep {
		padding: 20px;
		min-height: 300px;
	}

	.selected-preview.svelte-ikvep {
		padding: 16px;
		border-top: 1px solid #e5e7eb;
		background: #f9fafb;
	}

	.selected-preview.svelte-ikvep h4:where(.svelte-ikvep) {
		margin: 0 0 12px 0;
		font-size: 14px;
		font-weight: 600;
		color: #374151;
	}

	.preview-image.svelte-ikvep {
		display: flex;
		justify-content: center;
	}

	.preview-img.svelte-ikvep {
		width: 80px;
		height: 80px;
		object-fit: cover;
		border-radius: 8px;
		border: 2px solid #e5e7eb;
	}

	/* Responsive design */
	@media (max-width: 640px) {
		.image-picker.svelte-ikvep {
			max-width: 100%;
		}

		.tab-content.svelte-ikvep {
			padding: 16px;
			min-height: 250px;
		}

		.tab-button.svelte-ikvep {
			padding: 10px 12px;
			font-size: 14px;
		}
	}
